.cell {
  position: relative;
  width: 110px;
  height: 158px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  padding: 5px;
}

.cell__content {
  width: 100%;
  height: 100%;
  margin: auto auto;
  border: 1px solid #ffffff;
  border-radius: 3px;
}

.cell__card {
  position: absolute;
  top: 0;
  right: 0;
  user-select: none;
}
